html .editor-styles-wrapper h2,
h1,
h2,
h3,
h4,
h5,
h6 {
  color: currentColor;
}

.lia-text-center {
  text-align: center;
}

.lia-text-right {
  text-align: right;
}

.lia-text-left {
  text-align: left;
}

.lia-lheight-0 {
  line-height: 0;
}

.lia-flex-items-center {
  display: flex;
  align-items: center;
}

.lia-flex {
  display: flex;
}

.lia-flex-justify-center {
  display: flex;
  justify-content: center;
}

.lia-flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.lia-direction-column {
  flex-direction: column;
}

.lia-iflex-items-center {
  display: inline-flex;
  align-items: center;
}

.editor-styles-wrapper p.lia-mb-8,
.lia-mb-8 {
  margin-bottom: 8px;
}

.lia-ml-8 {
  margin-left: 8px;
}

.wp-core-ui .button-primary.lia-mr-8,
.wp-core-ui .button-secondary.lia-mr-8,
.lia-mr-8 {
  margin-right: 8px;
}

.lia-ml-auto {
  margin-left: auto;
}

.lia-mt-8 {
  margin-top: 8px;
}

.lia-mb-0,
.editor-styles-wrapper h4.lia-mb-0,
.editor-styles-wrapper h5.lia-mb-0,
.editor-styles-wrapper h6.lia-mb-0 {
  margin-bottom: 0;
}

.lia-w-full {
  width: 100%;
}

.lia-w-medium {
  width: 50%;
}

.lia-w-70p {
  width: 70%;
}

.lia-w-30p {
  width: 70%;
}

.lia-table {
  border-collapse: collapse;
}

.lia-table td,
.lia-table tr {
  border: 1px solid gray;
  padding: 10px;
}

.lia-recipe-image {
  position: relative;
}

.lia-image-upload__hover {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.7);
  transition: opacity 0.3s linear;
}

.lia-image-upload__hover:hover {
  opacity: 1;
}

.lia-shadow-sm {
  box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075);
}

.lia-seccion-container {
  max-width: 980px;
  margin-left: auto;
  margin-right: auto;
  padding: 30px;
  min-height: 250px;
}

.lia-section-repeater {
  padding-top: 10px;
  padding-bottom: 10px;
}

.block-editor__container img.lia-model-card__imagen {
  height: 100px;
  object-fit: contain;
}

.wp-core-ui .button.lia-model-card,
.wp-core-ui .button-secondary.lia-model-card {
  background-color: white;
}

.lia-model-card.is-active {
  border-color: red;
}

.lia-grid-50 {
  width: 50%;
}

.landings-inmobiliarias-panel-admin .components-notice {
  margin: 5px 15px 2px 0px;
}

.lia-quoter-projects__item {
  display: flex;
  border: 1px solid #e0e0e0;
  margin-bottom: 8px;
}

.lia-card__item {
  padding-left: 15px;
  padding-right: 15px;
  border: 1px solid #e0e0e0;
  margin-bottom: 8px;
}

.lia-quoter-projects__left {
  padding-top: 15px;
  padding-left: 15px;
  padding-bottom: 15px;
}

.lia-quoter-projects__right {
  padding: 10px 15px;
}

.lia-grid {
  display: flex;
}

.lia-item-10 {
  width: 10%;
}

.lia-item-20 {
  width: 20%;
}

.lia-item-30 {
  width: 30%;
}

.lia-item-40 {
  width: 40%;
}

.lia-item-50 {
  width: 50%;
}

.lia-item-60 {
  width: 60%;
}

.lia-item-70 {
  width: 70%;
}

.lia-item-80 {
  width: 80%;
}

.lia-item-90 {
  width: 90%;
}

.lia-wrap {
  flex-wrap: wrap;
}

.lia-gap-8 {
  margin-right: 8px;
}

@media (max-width: 540px) {
  .lia-grid {
    flex-wrap: wrap;
  }
  [class*=lia-item-] {
    width: 100%;
  }
  [class*=lia-gap-] {
    margin-right: 0;
  }
}